<template>
	<div id="login">
		<div class="login_box">
			<div class="login_left">
				<img src="../../../static/img/logo.png" alt="">
				<div class="jzty">
					<img src="../../../static/img/bj_text.png" alt="">
				</div>
			</div>
			<div class="login_right">
				<div class="login_right_cont">
					<div class="login_right_title">企程无忧建站系统</div>
					<div class="login_right_cont_input">
						<div class="login_right_cont_input_left">
							<img src="../../../static/img/icon2.png" alt="">
						</div>
						<div class="login_right_cont_input_right">
							<input type="text" v-model="form.name" placeholder="请输入账号名称">
						</div>
					</div>
					<div class="login_right_cont_input">
						<div class="login_right_cont_input_left">
							<img src="../../../static/img/icon1.png" alt="">
						</div>
						<div class="login_right_cont_input_right">
							<input type="password" v-model="form.pass" placeholder="请输入密码">
						</div>
					</div>
					<div class="login_btn" @click="sub">登录</div>
				</div>

			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
					pass: ''
				}
			}
		},
		methods: {
			sub() {
				if (this.form.name == '' ) {
					this.$message({
						showClose: true,
						message: '请填写账号名称',
						type: 'error'
					});
				} else if (this.form.pass == '') {
					this.$message({
						showClose: true,
						message: '请输入密码',
						type: 'error'
					});
				} else {
					let data = {
						mobile: this.form.name,
						password: this.form.pass
					}
					// console.log(data)
					this.$axios.post(this.$url + '/choujiang/land', data)
						.then((res) => {
							// console.log(res)
							if (res.data.msg == '用户不存在') {
								this.$message({
									message: res.data.msg,
									type: 'error'
								});
							} else if (res.data.msg == '密码错误') {
								this.$message({
									message: res.data.msg,
									type: 'error'
								});
							} else if (res.data.result) {
								this.$message({
									message: '登录成功',
									showClose: true,
									type: 'success'
								});
								localStorage.setItem('token', res.data.pojo.sessionid)
								let obj = {
									username: res.data.pojo.username
								}
								localStorage.setItem('Yhxx', JSON.stringify(obj))
								this.$router.push({
									name: 'admin_home'
								})
							} else if (res.data.msg == '用户已停用') {
								this.$message({
									message: res.data.msg,
									type: 'error'
								});
							}
						})
						.catch((res) => {
							// console.log(res)
						})
				}
			}

		}
	}
</script>

<style scoped>
	#login {
		width: 100%;
		height: 100vh;
		/* background: #121936; */
		overflow: hidden;
		/* background: radial-gradient(49% 160%,#22b5ff 0,#3a72fa 100%); */
	}

	.login {
		border-radius: 20px;
		width: 260px;
		background: #fff;
		padding: 50px 50px 60px 50px;
		position: fixed;
		top: 0;
		height: 215px;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		/* box-shadow: 0 8px 12px #545c64; */
	}

	.login div {
		margin: 10px auto;
	}

	.btn {
		width: 100% !important;
		background: #ff3a59 !important;
		color: white;
		outline: none;
		border: 0;
		transition: .5s;
		box-shadow: 0 3px 5px #CCC;
	}

	.btn:hover {
		background: #de2643 !important;
	}

	.login_box {
		width: 1214px;
		height: 714px;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		display: flex;
	}

	.login_left {
		width: 607px;
		height: 714px;
		background: #182B5C;
		box-shadow: 0px 2px 6px 3px rgba(231, 231, 231, 0.5);
		border-radius: 1px;
		filter: blur(0px);
		position: relative;
	}
	.login_left img{
		position: absolute;
		left: 40px;
		top: 45px;
	}
	.jzty{
		width: 370px;
		    height: 70px;
		    position: absolute;
		    top: 0;
		    left: 0;
		    right: 0;
		    bottom: 0;
		    margin: auto;
	}
	.jzty img{
		    width: 100%;
		    height: 100%;
		    left: 0;
		    top: 0;
	}

	.login_right {
		width: 607px;
		height: 714px;
		background: #FFFFFF;
		box-shadow: 0px 2px 6px 3px rgba(231, 231, 231, 0.5);
		border-radius: 1px;
		filter: blur(0px);
		position: relative;
	}

	.login_right_title {
		width: 100%;
		height: 33px;
		font-size: 24px;
		font-family: PingFangSC-Medium, PingFang SC;
		font-weight: 500;
		text-align: center;
		color: #182B5C;
		margin-bottom: 60px;
		line-height: 33px;
		font-weight: bold;
	}

	.login_right_cont {
		width: 330px;
		height: 335px;
		/* background: darkcyan; */
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
	}

	.login_right_cont_input {
		display: flex;
		justify-content: space-between;

		border: 1px solid #EAEAEA;
		margin-bottom: 30px;
	}

	.login_right_cont_input_left {
		width: 55px;
		/* background: darkgoldenrod; */
		height: 50px;
		position: relative;

		border-right: 1px solid #EAEAEA;
	}

	.login_right_cont_input_left img {
		width: 25px;
		height: 25px;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
	}

	.login_right_cont_input_right {
		width: 100%;
	}

	.login_right_cont_input_right input {
		width: 100%;
		height: 100%;
		outline: none;
		border: none;
		font-size: 14px;
		text-indent: 20px;
	}

	.login_btn {
		width: 330px;
		height: 48px;
		background: #182B5C;
		border-radius: 1px;
		color: white;
		text-align: center;
		line-height: 48px;
		margin-top: 50px;
		-webkit-user-select:none;
		
		   -moz-user-select:none;
		
		   -ms-user-select:none;
		
		   user-select:none;
	}
</style>



